:root {
    --darkest: #081c3d;
    --dark: #175676;
    --medium: #4ba3c3;
    --light: #cce6f4;
}

Header {
    width: 100%;
    height: 15vh;
    background-color: var(--dark);
    display: flex;
    flex-direction: column;
}
.header-top {
    height: 10vh;
    width: 100%;
    display: flex;
    color: var(--light);
    align-items: center;
}
.header-top img {
    border-radius: 250px;
    border: 2px solid var(--light);
    height: 90%;
    margin: 0 20px;
}

.header-nav {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.header-nav button {
    height: 50px;
    width: 120px;
    font-size: 1.5rem;
    font-weight: bolder;
    color: var(--dark);
    background-color: var(--light);
    border: none;
    border-radius: 15px;
}
@media only screen and (max-width: 780px) {
    .header-left img {
        display: none;
    }
    .header-left h1 {
        margin-left: 50px;
    }
}
@media only screen and (max-width: 560px) {
    .header-left h1 {
        margin-left: 10px;
    }
}
@media only screen and (max-width: 480px) {
    .header-left h1 {
        margin-left: 10px;
        font-size: 1.5rem;
    }
    .header-title {
        font-size: 1rem;
    }
}





